* {
  font-family: sans-serif;
  font-weight: bold;
}

.grid-area {
  margin-top: 30px;
  table {
    max-width: 700px;
    margin: auto;
    user-select: none;
    td {
      width: 20px;
      height: 20px;
      &.selected {
        color: rgb(233, 159, 0);
      }
    }
  }
}

